<template>
<div>
     <div id="app">
       <pre>
        {{code}}
        </pre>
        
     </div>
     <div v-html="styleCode">
   
     </div>
</div>
</template>

<script>
export default {
  name: 'App',
  components:{
   },
  data(){
    return{
      code:'',
       finalCode:'/*个人简历编写 \n姓名：廖世彪 \n性别：男 \n年龄：20 \n 联系方式：18807403426 邮箱2468442276@qq.com*/body{background:black;color:white}/*呀！界面不好看? \n */body{color:green;font-weight:bold; \n font-style:italic} \n \n \n我是毕业于湖南电子科技职业学院毕业，专业是计算机，这是我的GitHub：https://github.com/liaoshibiao 以及 博客：https://blog.csdn.net/tt22761 \n 本人技能：\n 1、 熟练使用JavaScript、JQuery 、mui、5+、Ajax数据交互；\n 2、熟练使用bootstrap的栅栏系统解决屏幕自适应布局 \n 3、熟悉Vue.js 、MongoDB、oralce数据库，熟练创建表格、增、删、改、查等操作 \n 4、熟悉面向对象编程思想，MVVM 设计模式 \n 5、会写微信小程序，只是没开发写过项目 \n 6、熟练使用Html5、Css3等； \n \n 之前呢在湖南长沙环球惠科技股份科技有限公司担任前端开发,在这公司有一年半多，之所以离职大概原因是因为公司团队解散，然后我就来到了上海寻求新的工作，\n 在之前公司主要还是做移动端。 \n 个人评价以及兴趣爱好:。\n 本人吃苦耐劳，有团队合作精神；专业知识过硬，对自己的专业有浓厚的兴趣。对工作认认真真，勤勤恳恳。具有一定的社交能力，具有优秀的组织和协调能力。在学习中，\n 注重理论与实践的结合，已具备了相当的实践操作能力，可独立学习和工作，以及拥有广泛的兴趣爱好，阅读、思考、运动、社交等为主要爱好,良好的生活与学习习惯!'
    }
  },created(){
  /// var string="12345678"
  var n = 0
    setInterval(()=>{
        this.code = this.finalCode.substring(0,n)
        n+=1
    },50)
  },
  computed:{
    styleCode(){
      return '<style>'+this.code+'</style>'
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}
</style>



